import { CqsItem } from "@rp/common/api-types"
import { FinanceDataTabs } from "../../../hooks/useWatchlistsFinanceDataTabNavigation"
import { useDataTabEntityLimiting } from "../../hooks/useDataTabEntityLimiting"
import { useCustomColumns } from "./useCustomColumns"
import { useCustomDataViewFinancialInput } from "./useCustomDataViewFinancialInput"

type UseCustomDataViewProps = {
  entities: Array<CqsItem>
  dataTab: FinanceDataTabs
  isFinancialPackageSubscriber: boolean
  isFeatureBlocked: boolean
}

export function useCustomDataView({
  entities,
  dataTab,
  isFinancialPackageSubscriber,
  isFeatureBlocked,
}: UseCustomDataViewProps) {
  const { hasCustomColumns } = useCustomColumns()
  const defaultTab: FinanceDataTabs = hasCustomColumns ? "custom" : "pricing"

  const { financialDataInput } = useCustomDataViewFinancialInput(dataTab)

  const { isCustomTabWithLimitedData, financialEntities } = useDataTabEntityLimiting({
    entities,
    dataTab,
    isFinancialPackageSubscriber,
    isFeatureBlocked,
  })

  return {
    defaultTab,
    financialDataInput,
    isCustomTabWithLimitedData,
    financialEntities,
  }
}
